import mpvuePicker from '../../../components/mpvue-picker/mpvuePicker.vue'
import uniCard from "@/components/uni-card/uni-card"

export default {
	data() {
		return {
			shopList: [{
				label: '',
				value: ''
			}],
			index: [0],
			mode: 'selector',
			rooms: []
		}
	},

	onLoad() {
		var that = this;
		uni.request({
			url: 'http://192.168.1.104:8080/gongyv_manage/api/fetchBuilding.action',
			data: {},
			method: "POST",
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success: function(res) {
				if (res.data) {
					that.shopList.pop();
					for (var i = 0; i < res.data.length; i++) {
						var item = {};
						item.label = res.data[i].buildingNumber;
						item.value = res.data[i].buildingId;
						item.rooms = res.data[i].roomList;
						that.shopList.push(item);
					}
					that.rooms = res.data[0].roomList;
				} else {
					that.$refs.popup.open();
				}
			},
		});
	},


	methods: {
		showPicker() {
			this.$refs.mpvuePicker.show();
		},
		onConfirm(e) {
			this.index = e.index
		},

		onChange(e) {
			this.index = e.index
			var buildingId = e.value;
			for (var i = 0; i < this.shopList.length; i++) {
				if (buildingId == this.shopList[i].value) {
					this.rooms = this.shopList[i].rooms;
					break;
				}
			}
		},
		onCancel(e) {},
		selectChange(e) {
			this.index = e.detail.value
		},
		showSinglePicker() {
			this.mode = 'selector'
			this.index = [0]
			this.$refs.mpvuePicker.show()
		}
	},
	components: {
		mpvuePicker,
		uniCard

	}
}
